/* Popup style */
.popup-box {
  position: fixed;
  background: #00000050;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}

.box {
  position: relative;
  width: 40%;
  margin: 0 auto;
  margin-left: 30%;
  height: auto;
  max-height: 70vh;
  margin-top: calc(100vh - 85vh + 25px);
  /* margin-top: 10%; */
  background: #fff;
  border-radius: 4px;
  padding: 20px;
  border: 1px solid #999;
  overflow: auto;
}

.close-icon {
  content: "x";
  cursor: pointer;
  position: fixed;
  right: calc(15% + 139px);
  top: calc(100vh - 85vh + 12px);
  /* top: 10%; */
  background: #ededed;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  line-height: 20px;
  text-align: center;
  border: 1px solid #999;
  font-size: 20px;
}

@media (max-width: 768px) {
  .close-icon {
    right: 0;
  }

  .box {
    width: 89%;
    margin-left: 0;
  }
}
